<template>
  <div id="app">
    <h1 class="glitch" data-text="CLEM">CLEM</h1>
    <h1 class="glitch" data-text="FREEMAN">FREEMAN</h1>
    <div class="center-box">
      <h2>web developer -> pdx</h2>
      <a href="http://clem.ninja/cv.html"><h2>curriculum vitae</h2></a>
      <canvas id="glitch-canvas"></canvas>
    </div>
    <img src="./assets/me.png" id="me" style="visibility:hidden">
  </div>
</template>

<script>
import glitch from 'glitch-canvas';
export default {
  ready() {
    this.randomGlitch();
  },
  methods: {
    randomGlitch() {
      const seed = Math.floor(Math.random() * 99);
      this.glitchMe(seed);
    },
    drawGlitch(imageData) {
      const canvasEl = document.getElementById('glitch-canvas');
      canvasEl.width = imageData.width;
      canvasEl.height = imageData.height;
      const ctx = canvasEl.getContext('2d');
      ctx.drawImage(imageData, 0, 0);
    },
    glitchMe(glitchSeed) {
      const image = new Image();
      image.src = document.getElementById('me').src;
      image.onload = () => {
        glitch({ seed: glitchSeed, quality: 50 })
        .fromImage(image)
        .toDataURL()
        .then((dataURL) => {
          const glitchedImg = new Image();
          glitchedImg.src = dataURL;
          this.drawGlitch(glitchedImg);
        });
      };
    },
  },
};
</script>

<style>
h2 {
  color: lightgrey;
}
.center-box {
  width: 400px;
  max-width: 400px;
}
#glitch-canvas {
  display: block;
}
</style>

<style src="./styles/glitch.scss" lang="scss"></style>


